import BigNumber from "bignumber.js";
import { ethers } from 'ethers'

export const lockwallet = async (apeContract: any, account: any, cnt: any) => {
  // const temp = await apeContract.methods.getNFTPrice().call()
  // const price = new BigNumber(temp)
  // const amount = price.multipliedBy(cnt)
  return apeContract.methods.selectLock(cnt).send({ from: account });
};

export const setApprove = async (
  wethContract: any,
  apeAddress: any,
  account: any
) => {
  return wethContract.methods
    .approve(apeAddress, ethers.constants.MaxUint256)
    .send({ from: account });
};



